//json json 
export function parseList(str) {
  if (!str) {
    return []
  }
  if (str instanceof Array) {
    return str;
  }
  try {
    let list = JSON.parse(str);
    if (list instanceof Array) {
      return list;
    } else {
      console.error("" + str + "");
      return [];
    }
  } catch (e) {
    console.error("" + str + "JSON");
    return [];
  }
}
