import Vue from 'vue'
import Vuex from 'vuex'
import User from "../model/user/User";
import Preference from '../model/preference/Preference'
import BrowserUtil from "../common/util/BrowserUtil";
import Cookies from "js-cookie"

Vue.use(Vuex)

let user = new User()
user.renderFromLocalStorage()

let lang = BrowserUtil.browserLang()
let localLang = Cookies.get("_lang");
if (localLang === "zh" || localLang === "en") {
  lang = localLang
}

const state = {
  config: {
    mobile: false,
    showDrawer: true
  },
  //
  versionName: '3.0.6',
  //
  user,
  breadcrumbs: [],
  //
  uploadMatters: [],
  //Matter List.vue
  uploadListInstance: null,

  //
  installed: true,

  //
  lang: lang,

  //
  preference: new Preference(),
  //
  lastLoginErrorTimestamp: 0

}

const getters = {
  getConfig(state) {
    return state.config
  }
}

const mutations = {}

const actions = {}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})
